import React, { PropTypes } from 'react'
import { Link } from 'react-router'
require('styles/common/header.styl')

import List from './list'

export default class Header extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      type: '',
      data: []
    }
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick (type) {
    let toggle = (this.state.type === type ? '' : type)
    let data = toggle ? this.props[toggle] : []
    this.setState({
      type: toggle,
      data: data
    })
  }

  closeList () {
    this.setState({
      type: '',
      data: []
    })
  }

  render () {
    return (
      <div className="head">
        <div className="logo">
          <Link className="link-logo" to="/"></Link>
        </div>
        <span onClick={() => { this.handleClick('topic')}}></span>
        <span onClick={() => { this.handleClick('section')}}></span>
        {this.state.type &&
          <List type={this.state.type} items={this.state.data} closeList={() => { this.closeList()}} />
        }
      </div>
    )
  }
}

Header.propTypes = {
  topic: PropTypes.array,
  section: PropTypes.array
}



